@import url(https://fonts.googleapis.com/css2?family=Handlee&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  background-color: #ebebeb;
  height: 60px;
  position: -webkit-sticky;
  position: sticky;
}
.top {
  position: fixed;
  width: 100%;
  z-index: 100;
  top: 0;
}
.searchBox {
  width: 100% !important;
  border: none;
  border-radius: 5px;
  outline: none;
  padding-left: 10px;
}
.section3 {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.nav__center {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.logout__button {
  display: none !important;
  margin-left: 5px;
  height: 40px;
}

@media only screen and (max-width: 600px) {
  .section1,
  .section3 {
    display: none;
  }
  .logout__button {
    display: inline !important;
  }
}

.post {
  margin-top: 20px;
  border: 5px solid #ebebeb;
  border-radius: 10px;
  padding: 20px;
}
.post__top {
  height: 40px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.icons {
  font-size: x-large;
  cursor: pointer;
}
.icon1 {
  margin-right: 20px;
}

.box {
  width: 100%;
  border: none;
  margin-bottom: 10px;
  outline: none;
  padding: 5px;
}

.post__bottom {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin-top: 20px;
}

.post__images {
  height: 100px;
  width: 150px;
  margin: 7px;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.post__imageBox {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 20px;
}
.image2 {
  margin-left: -10px;
  margin-top: -100px;
  cursor: pointer;
}
/* .t1,
.t3 {
  display: none;
} */
* {
  font-family: "Handlee", cursive;
}
@media only screen and (max-width: 600px) {
  .t1,
  .t3 {
    display: none;
  }
  .post {
    margin: 15px;
  }
  .post__images {
    height: 100px;
    width: 100px;
  }
}

.feed {
  /* width: 100%; */
  /* border: 5px solid #ebebeb; */
  border-radius: 10px;
  /* margin: 10px; */
  margin-bottom: 20px !important;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: auto;
}
.feed__body {
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.feed__content {
  min-height: 50px;
  height: auto;
  max-height: 500px;
  overflow-y: auto;
}
.feed__hrline {
  border-top: 1px inset #ebebeb;
  margin-top: 5px;
}
/* .feed__image {
  margin-top: 5px;
} */
.feed__text {
  word-wrap: break-word;
  word-break: break-all;
}

