.feed__post__images {
  border-radius: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.bigBox {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  /* width: 100%; */
  -webkit-align-items: center;
          align-items: center;
  margin-top: 20px;
  margin-bottom: 20px;
}
.box1 {
  margin-left: 20px;
}
.box2 {
  /* width: 100%; */
  padding-left: 10px;
  padding-right: 10px;
  /* display: flex; */
  /* flex-direction: column; */
  /* justify-content: center; */
}
.box3 {
  margin-right: 20px;
}

.arrow {
  cursor: pointer;
}
* {
  font-size: medium;
}
.feed__header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
}
.feed__header__text {
  margin-left: 10px;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.feed__header__avatar {
  height: 40px !important;
  width: 40px !important;
}
.feed__header__avatar__text {
  font-size: smaller;
}
@media only screen and (max-width: 600px) {
  .feed {
    margin: 10px;
    margin-bottom: 20px !important;
  }
}

